@use 'base/variables' as v;
@use 'base/mixins' as m;

//Botones
.btn {
	display: inline-block;
    @include m.tablet{
        display: inline;
        width: auto;
    }
	font-weight: 400;
	line-height: 1.5;
	text-align: center;
	text-decoration: none;
	vertical-align: middle;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	user-select: none;
	border: 1px solid transparent;
	padding: .375rem 2rem;
	font-size: 2rem;
	border-radius: .25rem;
	transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    margin: 1rem 0;
    &:hover {
        cursor: pointer;
    }
}

.btn-primary{
    color: #fff;
    background-color: v.$primary;
    border-color: v.$primary;
    &:hover {
        background-color: darken( v.$primary, 25% );
    }
}

.btn-secondary{
    color: #fff;
    background-color: v.$secondary;
    border-color: v.$secondary;
    &:hover {
        background-color: darken( v.$secondary, 25% );
    }
    
}

.btn-eliminar{
    color: #fff;
    background-color: v.$rojo-alerta;
    border-color: v.$rojo-alerta;
    &:hover {
        background-color: darken( v.$rojo-alerta, 25% );
    }
}

.btn-modificar{
    color: #fff;
    background-color: v.$naranja;
    border-color: v.$naranja;
    &:hover {
        background-color: darken( v.$naranja, 25% );
    }
}

.btn-select{
        background-color: darken( v.$secondary, 25% );
        p{
            color: v.$blanco;
        }
        &:hover {
            background-color: v.$secondary;
            cursor: pointer;
        }
}

.paginacion{
    display: flex;
    justify-content: space-between;
}

