/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: white;
  padding: 0.375rem 0.75rem;
}
a:hover {
  background-color: rgba(255, 255, 255, 0.11);
}

img {
  max-width: 100%;
}

form {
  margin: 2rem 0.5rem;
}
form label {
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.5;
  width: 10rem;
  padding: 0.5rem 0;
}
form .campo {
  display: grid;
  grid-template-columns: 1fr 4fr;
  margin: 1rem 0;
}
form input {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
form .disabled {
  filter: brightness(55%);
}

.alerta {
  padding: 0.5rem 2rem;
  margin: 0.5rem auto;
  color: #FFFFFF;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
}

.alertajs {
  width: 95%;
  position: fixed;
  padding: 0.5rem 2rem;
  margin: 0.5rem auto;
  color: #FFFFFF;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  opacity: 0;
  -webkit-transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}
@media (min-width: 768px) {
  .alertajs {
    width: 47.5%;
  }
}

.error {
  background-color: rgba(203, 0, 0, 0.6156862745);
  opacity: 1;
  -webkit-transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}

.exito {
  background-color: rgba(50, 159, 0, 0.6117647059);
  opacity: 1;
  -webkit-transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}

.mensaje {
  font-size: 2rem;
  text-align: center;
  background-color: hsla(0deg, 0%, 100%, 0.11);
  transform: scale(1.01);
}

.icon {
  width: 3rem;
  fill: white;
}
.icon:hover {
  filter: contrast(100%);
  transform: scale(1.05);
}

.display-none {
  display: none;
}

.display-block {
  display: block !important;
}

.visibility-true {
  opacity: 1;
  -webkit-transition: opacity 0.5s linear;
  -moz-transition: opacity 0.5s linear;
  -ms-transition: opacity 0.5s linear;
  -o-transition: opacity 0.5s linear;
  transition: opacity 0.5s linear;
}

.visibility-hidden {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.5s linear;
  -moz-transition: opacity 0.5s linear;
  -ms-transition: opacity 0.5s linear;
  -o-transition: opacity 0.5s linear;
  transition: opacity 0.5s linear;
}

.grid-1-4 {
  display: grid;
  grid-template-columns: 1fr 4fr;
}

.display-info {
  border: 1px solid #0da6f3;
  padding: 0 1.5rem;
}

.text-left {
  text-align: left;
}

.text-rigth {
  text-align: right;
}

.text-center {
  text-align: center;
}

ul {
  list-style: none;
  padding: 0 0 0 0.5rem;
}

.hover-resalt:hover {
  background-color: rgba(255, 255, 255, 0.11);
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
  margin: 2rem 0;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
    overflow: scroll;
  }
}
.contenedor-app .imagen {
  height: 30rem;
  background-image: url("../img/1.jpg");
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: 100%;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
}
.contenedor-app .main {
  padding-top: 2rem;
}
.contenedor-app .nav-bar {
  position: fixed;
  width: 95%;
  margin: 0 auto 2rem auto;
}
@media (min-width: 768px) {
  .contenedor-app .nav-bar {
    width: 47.5%;
  }
}
.contenedor-app .nav-bar .menu-user {
  display: flex;
  justify-content: space-between;
  height: 5rem;
}
.contenedor-app .nav-bar .datos-usuario {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
.contenedor-app .nav-bar .nombre-usuario {
  padding: 0 1.5rem;
}
.contenedor-app .nav-bar .avatar {
  cursor: pointer;
}
.contenedor-app .nav-bar .option {
  border: 1px solid white;
}
.contenedor-app .nav-bar .option a {
  margin: 1rem;
  font-weight: 400;
  line-height: 1.5;
  font-size: 1.8rem;
  padding: 0;
}

.links {
  display: flex;
  justify-content: space-between;
}

.seccion {
  text-align: center;
  display: none;
}

.display-info p {
  font-size: 1.5rem;
}

.servicio {
  display: flex;
  justify-content: space-between;
  background-color: #FFFFFF;
  border: 1px solid #0d6efd;
  padding: 0 1.5rem;
  transition: transform;
  transition-duration: 300ms;
}
.servicio:hover {
  cursor: pointer;
  background-color: #0da6f3;
}
.servicio:hover p, .servicio:hover .precio-servicio {
  color: #FFFFFF;
  transform: scale(1.05);
}
.servicio p {
  color: #1a1b15;
}
.servicio .precio-servicio {
  color: #0d6efd;
  font-weight: 700;
}

.servicio-adm {
  background-color: #FFFFFF;
  border: 1px solid #0d6efd;
  padding: 0 1.5rem;
  transition: transform;
  transition-duration: 300ms;
  border-width: 0;
}
.servicio-adm:hover {
  cursor: pointer;
  background-color: #0da6f3;
}
.servicio-adm:hover p, .servicio-adm:hover .precio-servicio {
  color: #FFFFFF;
  transform: scale(1.05);
}
.servicio-adm p {
  color: #1a1b15;
}
.servicio-adm .precio-servicio {
  color: #0d6efd;
  font-weight: 700;
}

.admin .fecha {
  max-width: 30rem;
  text-align: center;
  margin: 0 auto;
}
.admin .citas li {
  list-style: none;
}
.admin .citas .cita {
  border-bottom: 1px solid white;
}
.admin .citas .cita p {
  color: #0da6f3;
}
.admin .citas .cita p span {
  color: white;
}
.admin .acciones {
  display: grid;
}
.admin .acciones .btn {
  margin: 0.5rem;
}

.nav-admin {
  display: flex;
  justify-content: space-around;
  border: solid 1px #fff;
}
.nav-admin .btn {
  margin: 0;
  padding: 1rem;
  display: inline-block;
  width: 100%;
}

.option-adm {
  position: relative;
}
.option-adm .option {
  position: fixed;
  bottom: 4rem;
  right: 4rem;
  border-radius: 100%;
  padding: 0;
  margin: 0;
  line-height: 0;
}
.option-adm .option .icon {
  padding: 0;
}
.option-adm .option svg {
  border-radius: 100%;
  width: 4rem;
  height: 4rem;
}
.option-adm .option-add svg {
  background-color: #fff;
  fill: #0d6efd;
}
.option-adm .option-back svg {
  background-color: rgb(233, 161, 54);
  fill: #FFFFFF;
}

.btn {
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  padding: 0.375rem 2rem;
  font-size: 2rem;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .btn {
    display: inline;
    width: auto;
  }
}
.btn:hover {
  cursor: pointer;
}

.btn-primary {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
}
.btn-primary:hover {
  background-color: #013889;
}

.btn-secondary {
  color: #fff;
  background-color: #0da6f3;
  border-color: #0da6f3;
}
.btn-secondary:hover {
  background-color: #06537a;
}

.btn-eliminar {
  color: #fff;
  background-color: rgba(203, 0, 0, 0.6156862745);
  border-color: rgba(203, 0, 0, 0.6156862745);
}
.btn-eliminar:hover {
  background-color: rgba(76, 0, 0, 0.6156862745);
}

.btn-modificar {
  color: #fff;
  background-color: rgb(233, 161, 54);
  border-color: rgb(233, 161, 54);
}
.btn-modificar:hover {
  background-color: #905c10;
}

.btn-select {
  background-color: #06537a;
}
.btn-select p {
  color: #FFFFFF;
}
.btn-select:hover {
  background-color: #0da6f3;
  cursor: pointer;
}

.paginacion {
  display: flex;
  justify-content: space-between;
}

.tabs .tab {
  width: 95%;
  text-transform: uppercase;
  font-size: 1, 6rem;
}
@media (min-width: 768px) {
  .tabs {
    display: flex;
    justify-content: space-around;
    gap: 1rem;
  }
}

table {
  width: 100%;
  border-collapse: collapse;
}
table tr td {
  padding-left: 0.5rem;
  text-align: center;
}