@use 'base/variables' as v;
@use 'base/mixins' as m;

.admin{
    .fecha{
        max-width: 30rem;
        text-align: center;
        margin: 0 auto;
    }
    .citas{
        
        li{
            list-style: none;
        }
        .cita {           
            border-bottom: 1px solid white;
            p{
               color: v.$secondary;
               span{
                   color: white;
               }
            }
        }

    }

    .acciones{
        display: grid;
        .btn{
            margin: 0.5rem;
        }
    }


}

.nav-admin{
    display: flex;
    justify-content: space-around;
    border: solid 1px #fff;
    .btn{
        margin: 0;
        padding: 1rem;
        display: inline-block;
        width: 100%;
    }
}

.option-adm{
    position: relative;
    .option{              
        position: fixed;
        bottom: 4rem;
        right: 4rem;
        border-radius: 100%;
        padding: 0;
        margin: 0;
        line-height: 0;
        .icon{
            padding: 0;
        }
         svg{
            border-radius: 100%;
            width: 4rem;
            height: 4rem;           
        }
    }
    .option-add{
        svg{
            background-color: #fff;
        fill: v.$primary;
        }               
    }
    .option-back{
        svg{
            background-color: v.$naranja;
            fill: v.$blanco;
        }
    }
}