@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app{
    @include m.tablet{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
    overflow: scroll;
    }

    .imagen{
        height: 30rem;
        background-image: url('../img/1.jpg');
        background-position: center center;
        background-size: cover;
        @include m.tablet{
            height: 100%;
        }
    }

    .app{
        width: 95%;
        margin: 0 auto;
    }

    .main{
        padding-top: 2rem;
    }    

    .nav-bar{
        position: fixed;
        width: 95%;
        margin: 0 auto 2rem auto;
        @include m.tablet{
            width: 47.5%;
        }

        .menu-user{
            display: flex;
            justify-content: space-between;
            height: 5rem    ;
        }
        
        .datos-usuario{
            display: flex;
            justify-content: space-between;
            cursor: pointer;
        }

        .nombre-usuario{
            padding: 0 1.5rem;
        }

        .avatar{
            cursor: pointer;
        }

        .option{
            border: 1px solid white;
            a{
                margin: 1rem;
                font-weight: 400;
                line-height: 1.5;
                font-size: 1.8rem;
                padding: 0;
            }
        }
    }

    
}


