@use 'base/variables' as v;
@use 'base/mixins' as m;


.servicio{
    display: flex;
    justify-content: space-between;
    background-color: v.$blanco;
    border: 1px solid v.$primary;
    padding: 0 1.5rem;
    transition: transform;
    transition-duration: 300ms;
    &:hover{
        cursor: pointer;
        background-color: v.$secondary;
        p, .precio-servicio{
            color: v.$blanco;
            transform: scale(1.05);
        }
    }
    p{
        color: v.$negro;
    }
    .precio-servicio{
        color: v.$primary;
        font-weight: v.$bold;
    }
}

.servicio-adm{
    background-color: v.$blanco;
    border: 1px solid v.$primary;
    padding: 0 1.5rem;
    transition: transform;
    transition-duration: 300ms;
    border-width: 0;
    &:hover{
        cursor: pointer;
        background-color: v.$secondary;
        p, .precio-servicio{
            color: v.$blanco;
            transform: scale(1.05);
        }
    }
    p{
        color: v.$negro;
    }
    .precio-servicio{
        color: v.$primary;
        font-weight: v.$bold;
    }
}