@use 'variables' as v;
@use 'mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
body {
    min-height: 100%;
    @include m.tablet{
        height: 100%;
    }
    display: flex;
    flex-direction: column;
    background-color: v.$negro;
    color: v.$blanco;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
    color: white;
    padding: .375rem .75rem;  
        &:hover{
            background-color: rgba(255, 255, 255, 0.11);
        }
}


img {
    max-width: 100%;
}

//Form
form{

    margin: 2rem 0.5rem;
    label{
    font-size: 2rem;
    font-weight: 400;
    line-height: 1.5;
    width: 10rem;
    padding: 0.5rem 0;
    }
    .campo{
        display: grid;
        grid-template-columns: 1fr 4fr;
        margin: 1rem 0;
    }
    input{
    display: block;
    width: 100%;
    padding: .375rem .75rem;
    font-size: 2rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    }

    .disabled{
        filter: brightness(55%);
    }
}


.alerta{
    padding: 0.5rem 2rem;
    margin: 0.5rem auto;
    color: v.$blanco;
    text-align: center;
    font-weight: bold;
    text-transform: uppercase;
}

.alertajs{
    width: 95%;
    @include m.tablet{
        width: 47.5%;
    }
    position: fixed;
    padding: 0.5rem 2rem;
    margin: 0.5rem auto;
    color: v.$blanco;
    text-align: center;
    font-weight: bold;
    text-transform: uppercase; 
    opacity: 0;
    -webkit-transition: opacity 0.5s ease-in-out;
    -moz-transition: opacity 0.5s ease-in-out;
    -ms-transition: opacity 0.5s ease-in-out;
    -o-transition: opacity 0.5s ease-in-out;
    transition: opacity 0.5s ease-in-out;   
}

.error{
    background-color: v.$rojo-alerta;
    opacity: 1;
    -webkit-transition: opacity 0.5s ease-in-out;
    -moz-transition: opacity 0.5s ease-in-out;
    -ms-transition: opacity 0.5s ease-in-out;
    -o-transition: opacity 0.5s ease-in-out;
    transition: opacity 0.5s ease-in-out;
}

.exito{
    background-color: v.$verde-alerta;
    opacity: 1;
    -webkit-transition: opacity 0.5s ease-in-out;
    -moz-transition: opacity 0.5s ease-in-out;
    -ms-transition: opacity 0.5s ease-in-out;
    -o-transition: opacity 0.5s ease-in-out;
    transition: opacity 0.5s ease-in-out;
}

.mensaje{
    font-size: 2rem;
    text-align: center;
    background-color: hsla(0,0%,100%,.11);
    transform: scale(1.01);
}

.icon{
    width: 3rem;
    fill: white;
    &:hover{
        filter: contrast(100%);
        transform: scale(1.05);
    }
}

.display-none{
    display: none;
}

.display-block{
    display: block!important;
}

.visibility-true{
    opacity: 1;
    -webkit-transition: opacity 0.5s linear;
    -moz-transition: opacity 0.5s linear;
    -ms-transition: opacity 0.5s linear;
    -o-transition: opacity 0.5s linear;
    transition: opacity 0.5s linear;
}

.visibility-hidden{
    visibility: hidden;
    opacity: 0;
    -webkit-transition: opacity 0.5s linear;
    -moz-transition: opacity 0.5s linear;
    -ms-transition: opacity 0.5s linear;
    -o-transition: opacity 0.5s linear;
    transition: opacity 0.5s linear;
}

.grid-1-4{
    display: grid;
    grid-template-columns: 1fr 4fr;
}

.display-info{
    border: 1px solid v.$secondary;
    padding: 0 1.5rem;
}

.text-left{
    text-align: left;
}

.text-rigth{
    text-align: right;
}

.text-center{
    text-align: center;
}

ul{
    list-style: none;
    padding: 0 0 0 0.5rem;
}

.hover-resalt{
    &:hover{
        background-color: rgba(255, 255, 255, 0.11);
    }
}